.Exhibit__Icon__Box {
    width: 0px;
    height: 0px;
}

.Exhibit__Icon__Box.Box--Outer {
    -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
            box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
    position: relative;
    border-bottom: 120px solid var(--bright-cold);
    border-left: 120px solid var(--dark-cold);
}

.Exhibit__Icon__Box.Box--Inner {
    -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
            box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
    position: absolute;
    margin-left: -100px;
    margin-top: 20px;
    border-bottom: 80px solid var(--dark-warm);
    border-left: 80px solid var(--bright-warm);
    -webkit-transition: -webkit-transform .25s ease-in-out;
    transition: -webkit-transform .25s ease-in-out;
    -o-transition: transform .25s ease-in-out;
    transition: transform .25s ease-in-out;
    transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
}

.Exhibit__Icon__Box.Box--Inner:hover {
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}

.Exhibit__Icon__Box.Box--Inner:hover+.Exhibit__Icon__Circle.Circle--Top {
    -webkit-transform: translate(0px, 20px);
        -ms-transform: translate(0px, 20px);
            transform: translate(0px, 20px);
}

.Exhibit__Icon__Circle {
    -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
            box-shadow: 2px 2px 4px rgba(0, 0, 0, .2);
    position: absolute;
    z-index: 4000;
    width: 15px;
    height: 15px;
    border-radius: 15px;
}

.Exhibit__Icon__Circle.Circle--Top {
    margin-top: 92px;
    margin-left: -68px;
    background-color: var(--accent-warm);
    -webkit-transition: -webkit-transform .25s ease-in-out;
    transition: -webkit-transform .25s ease-in-out;
    -o-transition: transform .25s ease-in-out;
    transition: transform .25s ease-in-out;
    transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
}


